
*{-ms-touch-action:pan-y;touch-action:pan-y
}
.jsplumb-chart{-webkit-box-sizing:border-box;box-sizing:border-box;width:100%;height:100%;position:absolute
}
.jsplumb-chart .cavansClass{height:100%;width:100%;position:relative
}

*{-ms-touch-action:pan-y;touch-action:pan-y
}
.flowchart-node{height:100%;width:100%;position:relative
}
.flowchart-node .designIconBig{height:70px;width:150px;margin:0 auto;padding:12px;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-box-shadow:0 10px 18px -9px rgba(0,0,0,0.5);box-shadow:0 10px 18px -9px rgba(0,0,0,0.5);background:#ffffff;text-align:center;position:absolute;margin-right:15px;margin-bottom:20px;float:left
}
.flowchart-node .designIconBig i{float:none !important;position:absolute;left:5px;top:8px;width:30px !important;height:30px !important;line-height:30px !important;font-size:30px !important
}
.flowchart-node .designIconBig h4{position:absolute;top:5px;left:38px;margin:0px;padding:2px 0;width:110px;text-align:left;font-size:14px;font-weight:bold;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
.flowchart-node .designIconBig h5{position:absolute;top:25px;left:38px;margin:0px;padding:0px;width:110px;text-align:left;font-size:12px;font-weight:normal;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
.flowchart-node .designIconBig #removeDes{position:absolute;top:46px;right:15px;font-size:14px;color:#b9c0d8;margin:0px;padding:0px
}
.flowchart-node .designIconBig #copeDes{position:absolute;top:45px;right:35px;font-size:14px;color:#b9c0d8;margin:0px;padding:0px
}
.flowchart-node .designIconBig #pitchOnDes{position:absolute;top:47px;right:60px;font-size:14px;color:#b9c0d8;margin:0px;padding:0px
}
.flowchart-node .desingIconBig #markDes{position:absolute;top:45px;right:20px;font-size:14px;color:#b9c0d8;margin:0px;padding:0px
}
.flowchart-node .t1Style{border:2px solid #48c038;color:#48c038;border-radius:2px
}
.flowchart-node .t2Style{border:2px solid #4586f3;color:#4586f3;border-radius:2px
}
.flowchart-node .t3Style{border:2px solid #8367df;color:#8367df;border-radius:2px
}
.flowchart-node .redStyle{border:2px solid red
}
.flowchart-node .designIconBg{position:absolute;color:#ffffff !important
}
.flowchart-node .designIconBig #removeDes:hover{color:#ff4e4e
}
.flowchart-node .designIconBig #copeDes:hover{color:#ff4e4e
}
.flowchart-node .designIconBig #pitchOnDes:hover{color:#ff4e4e
}
.flowchart-node .line-split{position:absolute;height:100px;width:2px;background:#4586f3;right:-2px;top:0
}
